










































































































































































































































































































































































































import { Component, Prop, Vue } from 'vue-property-decorator'
import { useSearch } from '@/composables/Dashboard/useSearch'
import DateRangeFilter from '@/components/common/DateRangeFilter.vue'
import SearchColumnFilterComponent from '@/components/common/SearchColumnFilterComponent.vue'
import statusListComponent from '@/components/common/StatusList.vue'

import commonUtil from '@/util/common-util'
import { useDashboard } from '@/composables/Dashboard'
import can from '@/directives/can'
import { PaymentMethods } from '@/util/constants'

@Component({
  setup (props, context) {
    const { addRoutingSlip } = useDashboard(props, context)
    const {
      headerSearch,
      displayedHeaderSearch,
      status,
      routingSlipNumber,
      receiptNumber,
      dateFilter,
      folioNumber,
      entityNumber,
      totalAmount,
      chequeReceiptNumber,
      searchRoutingSlipResult,
      applyDateFilter,
      searchNow,
      debouncedSearch,
      canShowColumn,
      getStatusLabel,
      searchParamsExist,
      clearFilter,
      formatFolioResult,
      showExpandedFolio,
      showExpandedCheque,
      toggleFolio,
      toggleCheque,
      isLoading,
      navigateTo,
      fasUrl
    } = useSearch(props, context)
    return {
      headerSearch,
      displayedHeaderSearch,
      status,
      routingSlipNumber,
      receiptNumber,
      dateFilter,
      folioNumber,
      entityNumber,
      totalAmount,
      chequeReceiptNumber,
      searchRoutingSlipResult,
      applyDateFilter,
      searchNow,
      debouncedSearch,
      canShowColumn,
      getStatusLabel,
      addRoutingSlip,
      searchParamsExist,
      clearFilter,
      formatFolioResult,
      showExpandedFolio,
      showExpandedCheque,
      toggleFolio,
      toggleCheque,
      isLoading,
      navigateTo,
      fasUrl
    }
  },
  components: {
    DateRangeFilter,
    SearchColumnFilterComponent,
    statusList: statusListComponent
  },
  directives: {
    can
  }
})
export default class Search extends Vue {
  public colors = commonUtil.statusListColor
  public appendCurrencySymbol = commonUtil.appendCurrencySymbol
  public formatDisplayDate = commonUtil.formatDisplayDate

  PaymentMethods = PaymentMethods

  @Prop({ default: () => false }) isLibraryMode: boolean
}
